import React from 'react';
import {Dropdown} from 'semantic-ui-react';

import typeStyle from './style/type.module.scss';

class Topbar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            modes: [{
                key: "random",
                value: "random",
                text: "random"
            }, {
                key: "paragraphs",
                value: "paragraphs",
                text: "paragraphs"
            }]
        }
    }

    render() {
        return (
            <div className={typeStyle.top}>
                <Dropdown
                    placeholder={"modes"}
                    selection
                    options={this.state.modes}
                />
            </div>
        )
    }
}

export default Topbar;